import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";
import {FBListOptions} from "./FBList.Options";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";

export class FBListModel extends FBFieldWithPriceBaseModel<FBListOptions,FBColumnModel>{
    Rows:FBListItem[];

    constructor(Options: FBListOptions, Parent: FBColumnModel) {
        super(Options, Parent);



    }


    GetDynamicFieldNames(): string[] {
        return ['FBList'];
    }

    public CreateRow(row:string[]=[]) {
        if(!this.Options.EnableMultipleColumns)
            return new FBListItem([row.length>0?row[0]:'']);
        else
            return new FBListItem(this.Options.Columns.map((x,index)=>row.length>index?row[index]: ''));

    }

    GetStoresInformation(): boolean {
        return true;
    }



    public GetValue() {
        let itemsToReturn:string[][];
        itemsToReturn=[];

        for(let item of this.Rows)
            if(item.Columns.some(x=>x.trim()!=''))
                itemsToReturn.push(item.Columns);
        return itemsToReturn;

    }

    protected InternalSerialize(obj: any) {
        super.InternalSerialize(obj);
        obj.Value=this.GetValue();
        obj.Columns=this.Options.Columns.map(x=>x.ToObject());
    }

    GetIsUsed(): boolean {
        return this.GetValue().length>0;
    }

    RecreateRow() {
        this.Rows=[this.CreateRow()];
        this.Refresh();
    }

    InitializeStartingValues(previousData?: any) {
        let previousValues:string[][]=this.GetPreviousDataProperty('Value',[]);
        let valueToUse:FBListItem[]=[];
        if(previousValues.length==0)
            valueToUse=[this.CreateRow()];
        else
            for(let currentValue of previousValues)
                valueToUse.push(this.CreateRow(currentValue));

        this.Quantity=this.GetPreviousDataProperty( 'Quantity',this.ParseNumber(this.Options.QuantityDefaultValue));
        this.Rows=valueToUse;
    }
}




export class FBListItem {
    public Id:number;
    private static _lastId:number=0;
    public constructor(public Columns: string[])
    {
        this.Id=++FBListItem._lastId;
    }

}