import './FBList.css'
import * as React from "react";
import {
    FBFieldWithPriceBase,
    FBFieldWithPriceBaseProps,
    FBFieldWithPriceBaseState
} from "FormBuilder/Fields/Core/FBFieldWithPriceBase";
import {OptionsCalculator} from "FormBuilder/Core/Calulators/OptionsCalculator";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";

import {FBFieldBase, FBFieldBaseProps, FBFieldBaseState} from "FormBuilder/Fields/Core/FBFieldBase";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {GetFieldOptions} from "FormBuilder/Events/GetFieldOptions";
import {GetField} from "FormBuilder/Events/GetField";
import {FBListOptions} from "./FBList.Options";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {css} from "emotion";
import {faPlusCircle} from "@fortawesome/free-solid-svg-icons/faPlusCircle";
import {faMinusCircle} from "@fortawesome/free-solid-svg-icons/faMinusCircle";
import {FBListItem, FBListModel} from "./FBList.Model";
import {GetModel} from "FormBuilder/Events/GetModel";
import {FBRepeaterModel} from "../FBRepeater/FBRepeater.Model";
import {FBRepeaterOptions} from "../FBRepeater/FBRepeater.Options";
import {EventManager} from "shared/core/Events/EventManager";


export class FBList extends FBFieldWithPriceBase<FBListProps,FBListState>{

    constructor(props: FBListProps) {
        super(props);





    }

    static defaultProps: Partial<FBListProps> = {

    };







    public SubRender() {
        if(this.props.Model.Options.ShowQuantitySelector)
        {
            return(
                this.Model.Options.QuantityPosition == "bottom" || this.Model.Options.QuantityPosition == "right" ?
                    <React.Fragment>
                        {this.GetInput(this.Model.Options.QuantityPosition)}
                        {this.GetQuantityInput()}

                        {this.GetPriceOrRequiredBox()}

                    </React.Fragment>:
                    <React.Fragment>
                        {this.GetQuantityInput()}
                        {this.GetInput(this.Model.Options.QuantityPosition)}

                        {this.GetPriceOrRequiredBox()}

                    </React.Fragment>
            )
        }
        return(
            <React.Fragment>
                {this.GetInput('')}

                {this.GetPriceOrRequiredBox()}

            </React.Fragment>
        )

    }

    GetQuantityInput(){
        return(
            <div className={'rnTextFieldQuantity '+ this.props.Model.Options.QuantityPosition}>
                <div className={'rednaoLabel'}>
                    <label style={{fontWeight:'bold'}}>{this.props.Model.Options.QuantityLabel}</label>
                </div>
                <input className={'rnInputQuantity'} placeholder={this.Model.Options.QuantityPlaceholder} style={{width:'100%'}} type={'text'} value={this.Model.Quantity}
                       onChange={x=>{this.Model.Quantity=this.Model.ParseNumber(x.target.value as any);this.Model.Refresh()}}/>
            </div>

        )
    }


    GetInput(additionalClassNames:string){
        return (
            <div style={{width:'100%'}} className={'rnList '+ additionalClassNames}>
                {this.GetLabel()}
                <table className={'rnListContainer'} style={{width:'100%',borderCollapse:'collapse'}}>
                    {this.Model.Options.EnableMultipleColumns&&this.Model.Options.Columns.some(x=>x.Name.trim()!='')&&
                        <thead>
                            <tr>
                                {this.Model.Options.Columns.map((column,index)=>
                                    <th key={index} className={index==this.Model.Options.Columns.length-1?'last':''}  style={{textAlign:"left",verticalAlign:'top'}}>{column.Name}</th>
                                )}
                            </tr>
                        </thead>
                    }
                    <tbody>
                        {this.GenerateRows()}
                    </tbody>
                </table>

            </div>

        );
    }




    ListValueChanged(row:FBListItem,columnIndex:number,value:string)
    {
        row.Columns[columnIndex]=value;
        this.forceUpdate(()=>this.Model.FireValueChanged());
    }

    private GenerateRows() {
        return this.Model.Rows.map((row)=>
            <tr key={row.Id}>
                {row.Columns.map((column,index)=>
                    <td key={index} className={index==row.Columns.length-1?'last':''} >
                        <input type={'text'} style={{width:'100%'}} value={column} onChange={x=>this.ListValueChanged(row,index,x.target.value)}/>
                    </td>
                    
                    
                )}
                <td style={{verticalAlign:'middle',width:40}}>
                    <div style={{display:'flex'}} className={'rnButtonContainer'}>
                                    <span onClick={()=>this.CreateRowAt(row)}>
                                        <FontAwesomeIcon style={{marginRight:5,fontSize:18}} className={css`cursor:pointer;&:hover{color:green;}`} icon={faPlusCircle}/>
                                    </span>
                        {this.Model.Rows.length > 1 &&
                        <span onClick={() => this.RemoveRow(row)}>
                                                        <FontAwesomeIcon style={{fontSize: 18}}
                                                                         className={css`cursor:pointer;&:hover{color:red;}`}
                                                                         icon={faMinusCircle}/>
                                                    </span>
                        }
                    </div>
                </td>
            </tr>
        )
    }





    private CreateRowAt(row: FBListItem) {
        let clonedRow=this.Model.Rows.slice(0);
        clonedRow.splice(clonedRow.indexOf(row)+1,0,this.Model.CreateRow());
        this.Model.Rows=clonedRow;
        this.Model.FireValueChanged();
    }

    private RemoveRow(row: FBListItem) {
        let clonedRow=this.Model.Rows.slice(0);
        clonedRow.splice(clonedRow.indexOf(row),1);
        this.Model.Rows=clonedRow;
        this.Model.Refresh();
    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface FBListProps extends FBFieldWithPriceBaseProps{
    Model:FBListModel;

}

interface FBListState extends FBFieldWithPriceBaseState{

}


EventManager.Subscribe(GetModel,(event)=>{
    if(event.Options.Type==FieldTypeEnum.List)
        return new FBListModel(event.Options as FBListOptions,event.parent).Initialize(event.PreviousData);
});

EventManager.Subscribe(GetFieldOptions,(event)=>{
    if(event.Type==FieldTypeEnum.List)
        return new FBListOptions();
});

EventManager.Subscribe(GetField,(event)=>{
    if(event.Model.Options.Type==FieldTypeEnum.List)
        return <FBList Model={event.Model as FBListModel} />
});
//#endregion "My Region"