import {FBFieldWithPriceBaseOptions} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {StoreAutoIncrement, StoreBase, StoreDataType} from "shared/core/Store/StoreBase";



export class ListItemColumn extends StoreBase{

    @StoreAutoIncrement('ListItemColumn')
    public Id:number;
    public Name:string;
    protected LoadDefaultValues() {
        this.Id=0;
        this.Name='';
    }
    
}

export class FBListOptions extends FBFieldWithPriceBaseOptions{
    public Label;
    public EnableMultipleColumns:boolean;
    @StoreDataType(ListItemColumn)
    public Columns:ListItemColumn[];



    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Label='List';
        this.EnableMultipleColumns=false;
        this.Type=FieldTypeEnum.List;
        this.Columns=[];
    }


}

